import { Component } from '@angular/core';
import { IonicPage, NavController,LoadingController, NavParams } from 'ionic-angular';
import {SafeResourceUrl,DomSanitizer} from "@angular/platform-browser";

/**
 * Generated class for the PlayerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'video-player',
  templateUrl: 'video-player.html',
})
export class VideoPlayer {
  private trustedVideoUrl: SafeResourceUrl;
  private loading: any;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,public sanitizer: DomSanitizer,
              private loadingCtrl: LoadingController,

  ) {

  }

  ionViewDidLoad() {

  }
  ionViewWillEnter(): void {
    let Vid = `https://www.youtube.com/embed/${this.navParams.get('id')}` ;
    let desc = this.navParams.get('desc');
    console.log(desc);
     this.trustedVideoUrl = this.sanitizer.bypassSecurityTrustResourceUrl(Vid);

    this.loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });

    this.loading.present();

  }
  handleIFrameLoadEvent(): void {
    this.loading.dismiss();
  }

}



